/**
 * excelexcel,es6,ie11
 * (1)exceljs,github: https://github.com/exceljs/exceljs
 * (2)fielSaver.js, github: https://github.com/eligrey/FileSaver.js
 */


const excelJs = require('exceljs')  // exceljs
import { saveAs } from 'file-saver'

// excel
const exportDataToExcel = async (data, fileName, workSheetName="sheet1", headers=[])=>{
    console.log(data)
    const workBook = new excelJs.Workbook()  // 
    const workSheet = workBook.addWorksheet(workSheetName)  // 
    if(headers.length === 0){
        // ,data
        for(const key in data[0]){
            headers.push(key)
        }
    }
    workSheet.addRow(headers)  // excel
    data.forEach(item => {
        let row = []
        for(const key in item){
            row.push(item[key])
        }
        workSheet.addRow(row)  // 
    })
    const buf = await workBook.xlsx.writeBuffer()  // 
    console.log(buf)
    saveAs(new Blob([buf]), fileName + '.xlsx')  // excel
}

export {
    exportDataToExcel
}


