<template>
  <div>
    <!--  -->
    <el-row :gutter="3" style="margin-top: -10px">
      <el-col :span="2">
        <el-select
          v-model="selectedGroups"
          placeholder=""
          @change="getSelectedGroupInfos"
        >
          <el-option
            v-for="(item, index) in groups"
            :key="index"
            :label="item"
            :value="item"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="2">
        <el-button type="success" icon="el-icon-plus" @click="openGroupDialog"
          ></el-button
        >
      </el-col>
      <el-col :span="2">
        <el-button type="success" icon="el-icon-plus" @click="openItemDialog"
          ></el-button
        >
      </el-col>
      <el-col :span="2">
        <el-button
          type="success"
          icon="el-icon-folder-opened"
          @click="openItemsDialog"
          ></el-button
        >
      </el-col>
      <el-col :span="2">
        <el-button
          type="primary"
          icon="el-icon-download"
          @click="handleItemsDownload"
          ></el-button
        >
      </el-col>
      <el-col :span="2">
        <el-button type="primary" icon="el-icon-edit" @click="handleEditGroup"></el-button>
      </el-col>
      <el-col :span="2">
        <el-button type="danger" icon="el-icon-delete" @click="handleGroup"
          ></el-button
        >
      </el-col>
      <el-col :span="4">
        <el-input
          prefix-icon="el-icon-search"
          v-model="searchKeyWord"
          placeholder=""
        />
      </el-col>
      <el-col :span="2">
        <el-button type="primary" @click="handleSearch" icon="el-icon-search"
          ></el-button
        >
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <!-- table -->
    <el-table
      :data="itemData"
      border
      style="width: 100%; margin-top: -15px"
      v-loading="tableLoading"
      :element-loading-text="tableLoadingText"
    >
      <el-table-column
        v-for="(item, index) in tableColumns"
        v-if="index === 0"
        :key="Math.random()"
        :prop="item.prop"
        :width="indexWidth"
        align="center"
        label="Index"
        fixed="left"
      ></el-table-column>
      <el-table-column
        v-else-if="index === tableColumns.length - 1"
        fixed="right"
        align="center"
        label=""
        :key="Math.random()"
        :width="opWidth"
      >
        <template slot-scope="scope">
          <el-button @click="handleHisroty(scope.row)" type="text"
            ></el-button
          >
          <el-button type="text"></el-button>
          <el-button type="text"></el-button>
        </template>
      </el-table-column>
      <el-table-column
        v-else
        show-overflow-tooltip
        :prop="item.prop"
        align="center"
        :key="Math.random()"
        :label="item.label"
        :min-width="item.width"
      >
      </el-table-column>
    </el-table>
    <!--  -->
    <el-row style="margin-top: 10px">
      <el-pagination
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="itemCount"
        :current-page.sync="currentItemPage"
        @size-change="handleSizeChange"
        @current-change="handlePageChange"
        :page-sizes="[10, 15, 20]"
        :page-size="rowCount"
      ></el-pagination>
    </el-row>
    <!--  -->
    <el-dialog
      :title="groupDialogName"
      :visible.sync="groupDialog"
      width="800px"
      :showClose="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-form ref="groupForm" :model="groupForm" label-width="100px">
        <el-form-item label="GroupName">
          <el-input
            v-model="groupForm.groupName"
            placeholder=","
          ></el-input>
        </el-form-item>
        <el-form-item label="ColumnName">
          <el-input
            v-model="groupForm.columnName"
            placeholder=",,|"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleAddGroups"></el-button>
        <el-button @click="groupDialog = false"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="itemDialogName"
      :visible.sync="itemDialog"
      width="800px"
      :showClose="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-form label-width="100px">
        <el-form-item
          v-for="(item, index) in groupColumns"
          :label="item.label"
          :key="index"
        >
          <el-input v-model="item.model" ref="itemForm"> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleAddItem"></el-button>
        <el-button @click="itemDialog = false"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="itemDialogNames"
      :visible.sync="itemsDialog"
      v-loading="addItemLoading"
      element-loading-text="..."
      width="800px"
      :showClose="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-upload
        accept=".xls,.xlsx"
        drag
        :action="actionUrl"
        multiple
        :headers="uploadHeaders"
        :http-request="uploadFile"
        :before-upload="beforeUpload"
        :limit="limit"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">xlsxxls</div>
      </el-upload>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleAddItems"></el-button>
        <el-button @click="itemsDialog = false"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="historyDialogName"
      :visible="historyDialog"
      width="1000px"
      v-loading="chartLoading"
      element-loading-text="..."
      :showClose="false"
      :body="showChartBody"
      @opened="handleOpen"
    >
      <div class="block">
        <el-row>
          <el-col :span="14">
            <span class="demonstration" style="margin-left: 20px"
              ></span
            >
            <el-date-picker
              v-model="st"
              type="datetimerange"
              :picker-options="pickerOptions"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              align="right"
              @change="showHistory"
            >
            </el-date-picker>
          </el-col>
          <el-col :span="8">
            <el-form ref="form" label-width="80px">
              <el-form-item label="" style="margin-bottom: 0px">
                <el-select v-model="interval" placeholder="" @change="showHistory">
                  <el-option
                    v-for="(item, index) in intervals"
                    :key="index"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </div>
      <el-divider></el-divider>
      <div id="main" style="width: 900px; height: 400px"></div>
      <div slot="footer" class="dialog-footer">
        <el-button@click="handleDownloadData"></el-button>
        <el-button @click="historyDialog = false"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog>
      
    </el-dialog>
  </div>
</template>
<script>
import {
  getGroups,
  addGroups,
  getGroupColumns,
  addItem,
  deleteGroup,
  getItems,
  getRealTimeData,
  addItemsByExcel,
  getHistoryData,
} from "@/api/group"; // 
import { exportDataToExcel } from "@/utils/excel";
import axios from "axios";
const excelJs = require("exceljs"); // exceljs
import { saveAs } from "file-saver";
import { getCookie } from "@/utils/cookie";
import $ from "jquery";

export default {
  name: "Group",
  data: () => {
    return {
      selectedGroups: "", // 
      groups: [], // gdb
      groupDialog: false, // 
      groupDialogName: "",
      groupForm: {
        groupName: "",
        columnName: "",
      },
      groupColumns: [], // 
      itemDialog: false, // 
      itemDialogName: "",
      itemData: [], // 
      tableColumns: [], // 
      indexWidth: "60", // Index
      opWidth: "200", // 
      searchKeyWord: "", // 
      itemDialogNames: "", // title
      itemsDialog: false,
      actionUrl: "", // url
      uploadHeaders: { "Content-Type": "multipart/form-data" }, // 
      fileList: [], // 
      fileContent: null,
      limit: 1, // 
      startRow: 0, // row
      rowCount: 10, // 
      currentItemPage: 1, // 
      itemCount: 0, // 
      addItemLoading: false, // loading
      searchCondition: "itemName like '%%'", // searchKeyWord
      tableLoading: false, // table
      tableLoadingText: "...",
      historyDialogName: "",
      historyDialog: false,
      myChart: null,
      st: [
        new Date(new Date().setTime(new Date() - 3600 * 1000 * 24)),
        new Date(),
      ], // 
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 1);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      intervals: [
        { label: "1s", value: "1" },
        { label: "10s", value: "10" },
        { label: "30s", value: "30" },
        { label: "60s", value: "60" },
        { label: "120s", value: "120" },
        { label: "360s", value: "360" },
      ], // 
      interval: "60",  // 
      selectedItem: "",  // item
      chartItemValues: null,  // chart
      chartLoading: false,  
      showChartBody: document.getElementById("main")
    };
  },
  created() {
    // 
    getGroups().then(({ data }) => {
      this.groups = data.groupNames;
      this.selectedGroups = this.groups[0];
      this.render();
    });
    this.actionUrl = "http://" + getCookie("ip") + "/page/uploadFile";
  },
  methods: {
    // 
    openGroupDialog() {
      this.groupForm.groupName = "";
      this.groupForm.columnName = "";
      this.groupDialog = true;
    },
    // 
    handleAddGroups() {
      if (this.groupForm.groupName.length != 0) {
        const g = this.groupForm.groupName.split(","); // 
        const c = this.groupForm.columnName.split("|"); // 
        if (g.length != c.length) {
          this.$message.error("");
        } else {
          const d = [];
          for (let i = 0; i < g.length; i++) {
            if (c[i].length == 0) {
              d.push({ groupNames: g[i] });
            } else {
              d.push({ groupNames: g[i], columnNames: c[i].split(",") });
            }
          }
          addGroups(JSON.stringify(d))
            .then((r) => {
              this.$message.success("!");
              this.groupDialog = false;
              getGroups().then(({ data }) => {
                this.groups = data.groupNames;
                this.selectedGroups = this.groups[0];
              });
            })
            .catch(
              ({
                response: {
                  data: { message },
                },
              }) => {
                this.$message.error(message);
              }
            );
        }
      } else {
        this.$message.warning("");
      }
    },
    // 
    getSelectedGroupInfos() {
      this.render(); // 
    },
    // 
    openItemDialog() {
      this.itemDialog = true;
    },
    // 
    handleAddItem() {
      let values = [];
      let d = {};
      const f = this.$refs.itemForm;
      for (let i = 0; i < this.groupColumns.length; i++) {
        d[this.groupColumns[i]["label"]] = f[i].value;
      }
      values.push(d);
      addItem(
        JSON.stringify({
          groupName: this.selectedGroups,
          values,
        })
      )
        .then(() => {
          this.itemDialog = false;
          this.$message.success("!");
          this.render();
        })
        .catch(({ response: { data: message } }) => {
          this.$message.error(message);
        });
    },
    // 
    handleGroup() {
      this.$confirm(",?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          deleteGroup(
            JSON.stringify({
              groupNames: [this.selectedGroups],
            })
          ).then(() => {
            getGroups().then(({ data }) => {
              this.groups = data.groupNames;
              this.selectedGroups = this.groups[0];
              this.render();
              this.$message.success("!");
            });
          });
        })
        .catch(() => {});
    },
    // item
    showItems(groupName) {
      this.tableLoading = true;
      getItems(
        JSON.stringify({
          groupName: this.selectedGroups,
          column: "*",
          condition: this.searchCondition,
          startRow: this.startRow,
          rowCount: this.rowCount,
        })
      )
        .then(({ data: { itemCount, itemValues } }) => {
          this.itemCount = itemCount;
          this.itemData = [];
          let itemNames = [];
          if (itemValues != null) {
            for (let i = 0; i < itemValues.length; i++) {
              itemNames.push(itemValues[i].itemName);
            }
          }
          getRealTimeData(
            JSON.stringify({
              groupName,
              itemNames,
            })
          )
            .then((response) => {
              const timeData = response.data;
              for (let i = 0; i < itemNames.length; i++) {
                itemValues[i]["realTimeData"] =
                  timeData[itemNames[i]] === null ? "" : timeData[itemNames[i]];
                itemValues[i]["index"] = (this.startRow + i).toString();
              }
              this.itemData = itemValues;
              this.tableLoading = false;
            })
            .catch(({ response: { data: message } }) => {
              this.tableLoading = false;
              this.$message.error(message);
            });
        })
        .catch(({ response: { data: message } }) => {
          this.tableLoading = false;
          this.$message.error(message);
        });
    },
    // 
    render() {
      // 
      getGroupColumns(
        JSON.stringify({
          groupNames: [this.selectedGroups],
        })
      )
        .then(({ data }) => {
          this.showItems(this.selectedGroups);
          this.rowKey = "rowKey";
          const columns = data[this.selectedGroups].itemColumnNames;
          let c = [];
          let c1 = [{ prop: "index", label: "index" }]; // index
          this.tableColumns = [];
          const width =
            (1600 - parseInt(this.opWidth) - parseInt(this.indexWidth)) /
            (columns.length + 1);
          for (let i = 0; i < columns.length; i++) {
            c.push({ index: i + 1, label: columns[i], model: "" });
            c1.push({ prop: columns[i], label: columns[i], width: width });
          }
          c1.push({
            prop: "realTimeData",
            label: "realTimeData",
            width: width,
          }); // 
          c1.push({ prop: "op", label: "op" }); // 
          this.groupColumns = c; // groupColumnform
          this.tableColumns = c1; // 
        })
        .catch(
          ({
            response: {
              data: { message },
            },
          }) => {
            this.tableColumns = [];
            this.itemData = [];
          }
        );
    },
    // 
    openItemsDialog() {
      this.itemsDialog = true;
      this.fileList = [];
    },
    // 
    beforeUpload(file) {
      this.fileContent = file; // 
      if (this.isExcel(this.fileContent.name)) {
        return true;
      } else {
        this.$message.error("!");
        return false; // false
      }
    },
    // 
    uploadFile() {
      const data = new FormData();
      const fileUps = this.fileContent;
      data.append("file", fileUps);
      axios({
        headers: {
          "Content-Type": "multipart/form-data",
        },
        url: this.actionUrl,
        data: data,
        method: "post",
      })
        .then(() => {
          this.$message.success("");
        })
        .catch(({ response: { data: message } }) => {
          this.$message.error(message);
        });
    },
    // excel
    isExcel(name) {
      return /\.(xlsx|xls|csv)$/.test(name);
    },
    // 
    handleAddItems() {
      this.addItemLoading = true;
      addItemsByExcel(
        JSON.stringify({
          fileName: this.fileContent.name,
          groupName: this.selectedGroups,
        })
      )
        .then(() => {
          this.addItemLoading = false;
          this.itemsDialog = false;
          this.render();
          this.$message.success("");
        })
        .catch(({ response: { data: message } }) => {
          this.addItemLoading = false;
          this.$message.error(message);
        });
    },
    // 
    handleSizeChange(c) {
      this.rowCount = c;
      this.startRow = (this.currentItemPage - 1) * this.rowCount; // start index
      this.showItems(this.selectedGroups);
    },
    // 
    handlePageChange() {
      this.startRow = (this.currentItemPage - 1) * this.rowCount;
      this.showItems(this.selectedGroups);
    },
    // 
    handleSearch() {
      this.searchCondition = `itemName like '%${this.searchKeyWord}%'`;
      this.showItems(this.selectedGroups);
    },
    // 
    handleItemsDownload() {
      this.$confirm(`${this.itemCount}?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.tableLoadingText = "...";
          this.tableLoading = true;
          getGroupColumns(
            JSON.stringify({
              groupNames: [this.selectedGroups],
            })
          ).then(({ data }) => {
            const headers = data[this.selectedGroups].itemColumnNames;
            getItems(
              JSON.stringify({
                groupName: this.selectedGroups,
                column: "*",
                condition: this.searchCondition,
                startRow: -1,
              })
            )
              .then(({ data: { itemValues } }) => {
                const workBook = new excelJs.Workbook(); // 
                const workSheet = workBook.addWorksheet("Sheet1"); // 
                workSheet.addRow(headers); // excel
                if (itemValues != null) {
                  itemValues.forEach((item) => {
                    let row = [];
                    headers.forEach((key) => {
                      row.push(item[key]);
                    });
                    workSheet.addRow(row); // 
                  });
                }
                workBook.xlsx.writeBuffer().then((buf) => {
                  saveAs(
                    new Blob([buf]),
                    this.selectedGroups + "_" + new Date().getTime() + ".xlsx"
                  ); // excel
                }); // 
                this.$message.success("");
                this.tableLoadingText = "...";
                this.tableLoading = false;
              })
              .catch((e) => {
                this.$message.error(e.message);
              });
          });
        })
        .catch(() => {});
    },
    // 
    handleHisroty({ itemName }) {
      this.historyDialog = true;
      this.selectedItem = itemName;
    },
    // 
    showHistory() {
      this.chartLoading = true
      const s = new Date(this.parseTime(this.st[0])).getTime() / 1000;
      const e = new Date(this.parseTime(this.st[1])).getTime() / 1000;
      getHistoryData(
        JSON.stringify({
          groupName: this.selectedGroups,
          itemNames: [this.selectedItem],
          startTime: [s],
          endTime: [e],
          interval: parseInt(this.interval),
        })
      )
        .catch(({ response: { data: message } }) => {
          this.$message.error(message);
        })
        .then(({ data }) => {
          this.chartItemValues = data[this.selectedItem];
          if (
            this.myChart != null &&
            this.myChart != "" &&
            this.myChart != undefined
          ) {
            this.myChart.dispose(); //echarts dom
          }
          this.myChart = this.$echarts.init(document.getElementById("main"));
          this.myChart.setOption({
            title: {
              text: this.selectedItem,
              padding: [10, 10, 10, 50],
            },
            tooltip: {
              trigger: "axis",
            },
            xAxis: {
              data: this.chartItemValues[0].map((item) => {
                return this.parseTime(new Date(parseInt(item) * 1000))
              }),
            },
            yAxis: {
              splitLine: {
                show: false,
              },
            },
            toolbox: {},
            width: "900px",
            dataZoom: [
              {
                startValue: this.parseTime(new Date(parseInt(this.chartItemValues[0]) * 1000)),
              },
              {
                type: "inside",
              },
            ],
            series: {
              name: this.selectedItem,
              type: "line",
              data: this.chartItemValues[1],
            },
          });
          this.chartLoading = false
        });
    },
    handleOpen() {
      this.showHistory();
    },
    // 
    parseTime(t) {
      return `${t.getFullYear()}-${
        t.getMonth() + 1 < 10 ? "0" + (t.getMonth() + 1) : t.getMonth() + 1
      }-${t.getDate() < 10 ? "0" + t.getDate() : t.getDate()} ${
        t.getHours() < 10 ? "0" + t.getHours() : t.getHours()
      }:${t.getMinutes() < 10 ? "0" + t.getMinutes() : t.getMinutes()}:${
        t.getSeconds() < 10 ? "0" + t.getSeconds() : t.getSeconds()
      }`;
    },
    // 
    handleDownloadData() {
      this.chartLoading = true
      const workBook = new excelJs.Workbook(); // 
      const workSheet = workBook.addWorksheet("Sheet1"); // 
      const headers = ["Time", "Data"]
      workSheet.addRow(headers); // excel
      if (this.chartItemValues != null) {
        for(let i=0;i< this.chartItemValues[0].length;i++){
            let row = []
            row.push(this.parseTime(new Date(parseInt(this.chartItemValues[0][i]) * 1000)))
            row.push(this.chartItemValues[1][i])
            workSheet.addRow(row); // 
        }
      }
      workBook.xlsx.writeBuffer().then((buf) => {
        saveAs(
          new Blob([buf]),
          this.selectedGroups + "_" + this.selectedItem + "_" + new Date().getTime() + ".xlsx"
        ); // excel
      }); // 
      this.$message.success("");
      this.chartLoading = false;
    },
    // 
    handleEditGroup(){

    }
  },
};
</script>
<style scoped>
.el-select {
  width: 100%;
}
.el-autocomplete {
  width: 100%;
}
/*  */
.el-pagination {
  float: left;
}
.el-upload-dragger {
  width: 600px;
}
.el-divider--horizontal {
  margin: 10px 0;
  width: 100%;
}
</style>


